body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-arial: Arial;
  --font-helvetica: Helvetica;

  /* font sizes */
  --font-size-sm: 14px;

  /* Colors */
  --color-white: #fff;

  /* Paddings */
  --padding-xs-5: 11.5px;
  --padding-smi-5: 12.5px;
  --padding-mid: 17px;

  /* Border radiuses */
  --br-4xs: 9px;
}
